@font-face {
  font-family: "Werfus";
  src: url(fonts/Werfus.ttf) format("truetype");
}
@font-face {
  font-family: "Ogilvie";
  src: url(fonts/ogilvie.ttf) format("truetype");
}
@font-face {
  font-family: "Amsterdam";
  src: url(fonts/Amsterdam.ttf) format("truetype");
}
.screen-wrapper, .under-screen-wrapper, .under-screen-wrapper2 {
  width: 100%;
  position: absolute;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.screen-wrapper {
  top: 0;
}
.under-screen-wrapper {
  display: block;
  top: 100%;
}
.under-screen-wrapper2 {
  display: block;
  top: 200%;
}
.big-font {
  size: 4em;
}
.flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ugly, .beautiful, .user {
  text-align: center;
  padding: 5px;
  margin: 10px;
}
.ugly {
  font-family: 'ogilvie', serif;
}
.beautiful {
  color: #3100e4;
  font-family: 'Werfus', serif;
  font-size: 2.5em;
  font-width: 700;
}
.user {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  max-width: 50%;
  max-height: 50px;
  font-family: 'Amsterdam', serif;
  font-size: 2em;
  color: black;
}
.left-join {
  margin-left: 0 !important;
}
.rigth-join {
  margin-right: 0 !important;
}
