.modal-mask {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
}
@keyframes zoomIn {
from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
}
50% {
    opacity: 1;
}
}
.zoomIn {
  animation-name: zoomIn;
}
.modal-container {
  padding: 20px 40px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 8px rgba(0, 0, 0, 0.33);
  font-family: Helvetica, Arial, sans-serif;
}
.modal-header h3 {
  font-size: 2em;
  color: #42b983;
}
.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.modal-body {
  margin: 20px 20px;
}
.modal-footer {
  display: flex;
  justify-content: flex-end;
}
.modal-default-button {
  margin: 20px;
  font-size: 2em;
}
